.game{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 80%;
  height: 40%;
  margin-left: auto;
  margin-right: auto;

  background: #fafafa;
  border-radius: 10px;
}
button{
  background-color: #3498db;
  border: none;
  color: white;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  margin: 20px 0px;
}
.gameField{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
  width: 32vw;
  height: 32vw;
  border: 1px solid black;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background: orange;
  border-collapse: collapse;
}
.empty{
  visibility: hidden;

}
.cell{
  border-collapse: collapse;
  display: -ms-flexbox;
  display: flex;
  width: 25%;
  height:25%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: white;
  border: solid 1px black;


}
span{
  text-align: center;
  color: orange;
  font-size: 20px;
}



